.home,.news{
  h2{
    color:#fff;
    font-size:24px;
    margin: 0;
    line-height:50px;
    background: skyblue;
    text-align: center;
  }
  .avatar{
    width: 66px;
    height: 100px;
    border-radius:0;
  }
  .ant-list{
    margin:0 10px;
    .ant-list-item{
      position: relative;
      .ant-btn{
        position: absolute;
        right:15px;
        bottom:8px;
      }
    }
  }
  .ant-list-item-meta-description{
    word-break: break-all;
  }
}
.news{
  select{
    width: 50%;
    border: 1px solid #ccc;
    height: 30px;
    margin:10px 0;
    margin-left:80px;
  }
}
.loader{
  text-align: center;
  margin-bottom: 20px;
  margin: 20px 0;
}
